import Vue from 'vue'
import AlertComponent from './index.vue'
//
function merge(target) {
    for (let i = 1, j = arguments.length; i < j; i++) {
        let source = arguments[i] || {};
        for (let prop in source) {
            if (source.hasOwnProperty(prop)) {
                let value = source[prop];
                if (value !== undefined) {
                    target[prop] = value;
                }
            }
        }
    }
    return target;
};
let instance;
//extend .
let AlertConstructor = Vue.extend(AlertComponent);

let initInstance = ()=>{
    //ConfirmConstructor
    instance = new AlertConstructor({
        el: document.createElement('div')
    });
    //boby
    document.body.appendChild(instance.$el);
}
let Alert={}
Alert.install = () => {

    //
    initInstance();
    
    //Promise
    Vue.prototype.$alert = (options={}) => {
        return new Promise((resolve, reject)=>{
            //  confirm instance instance.$datamain.vuedata
            merge(instance.$data, options);
            instance.show = true;
            let success = instance.success;
            let cancel = instance.cancel;
            instance.success = () => {
                //instance.successmain.vuesuccess
                success();
                //
                resolve('ok');
            }
        });
    };

}
export default Alert;


