import Loading from './index.vue'

export default {
  /**
   * install
   * @param {Object} Vue - Vue
   * @param {Object} [pluginOptions] - 
   */
  install(Vue, pluginOptions = {}) {
    // ""
    const VueLoading = Vue.extend(Loading)
    let loading = null

    /**
     * loading
     * @returns {Promise} Promise
     */
    function $loading() {
      return new Promise((resolve) => {
        // 
        if (!loading) {
          loading = new VueLoading()
          // 
          loading.$mount()
          // 
          document.querySelector(pluginOptions.container || 'body').appendChild(loading.$el)
          console.log(loading.$el,11111111111)
        }
        // loading
        loading.show()
        resolve()
      })
    }
    // loading
    $loading.end = (noAnimate = false) => {
      return new Promise((resolve) => {
        if (!loading || !loading.isShow) {
          resolve()
          return
        }
        // 
        if (noAnimate) {
          // ,
          loading.removeAnimate().then(() => {
            loading.opemAnimate()
          })
        }

        loading.hide()
      })
    }

    Vue.loading = Vue.prototype.$loading = $loading
  },
}

